.contador{
    color: white;
    font-size: 10vh;
    display: flex;
   
    align-content: center;
    justify-content: center;
    width:70vh;
    height: auto;
    padding: 2vh;
    background-color: red;
    flex-wrap: wrap;
    margin-top: 2vh;
    margin-bottom: 5vh;
    border: solid white 1vh;
    border-radius: 100px;

}



@media screen and (max-width:770px) {
 
    .contador{
        color: white;
        border-radius: 50px;
        width:200%;
    
    }
  
}