
button{
    width: 50%;
    height: auto;
    margin: 5px;
    font-size: 5vh;
    border-radius: 100px;
    color:white;
    text-shadow: -0.1vh 0.1vh 0 #000,
    0.1vh 0.1vh 0 #000,
   0.1vh -0.1vh 0 #000,
  -0.1vh -0.1vh 0 #000;
    background-color: red;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding: 1vh;
    border: solid white 0.45vh;
}
button:hover{
    cursor: pointer;
    box-shadow:  -0.2vh -0.2vh 0.5vh 0.2vh black, 0.2vh 0.2vh 0.5vh 0.2vh black,-0.2vh 0.2vh 0.5vh 0.2vh black,0.2vh -0.2vh 0.5vh 0.2vh black;
    
    
    
    

}
button:active{
    color: white;
    background-color: white;
}
